header {
	text-align: center;
}
h1 {
	margin-top: 0;
	padding-top: 1.5rem;
}

body {
	color: azure;
	font-size: 2rem;
}

.background {
	position: absolute;
	z-index: -100;
	margin: 0;
	height: 100vh;
	width: 100vw;
	background-image: url('../images/circle-scatter-haikei.svg');
	background-repeat: repeat;
	background-size: cover;
	background-position: center;
}

.game-board-wrapper {
	height: fit-content;
	position: relative;
}

.reset {
	display: flex;
	justify-content: center;
}

.game-board {
	position: relative;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 1rem;
	margin: 20px auto;
}

.tile {
	border: solid white 2px;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100px;
	width: 100px;
	font-size: 4rem;
	cursor: pointer;
}

.container {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 40vh;
}

.strike {
	border-bottom: 10px solid greenyellow;
	position: absolute;
	width: 98%; /* Use relative width */
	left: 5%; /* Use relative positioning */
	height: 5px;
	border-radius: 30px;
}

.hidden {
	display: none;
}

.rows {
	transform: translateY(-50%); /* Center the strike vertically */
	transform: translateX(-4%);
}

.row1 {
	top: 15%;
}

.row2 {
	top: 48%; /* Adjust the position based on the desired location */
}

.row3 {
	bottom: 18%; /* Adjust the position based on the desired location */
}

.columns {
	top: 2.5%;
	width: 100%;
	transform: rotate(90deg);
	transform-origin: left bottom; /* Set the rotation origin */
}

.col1 {
	left: 13%;
}

.col2 {
	left: 48%;
}

.col3 {
	left: 83%;
}

.diagonal {
	top: 3.5%;
	transform: rotate(45.5deg);
	transform-origin: left bottom;
	left: 0%;
	width: 139%;
}

.reverse-diagonal {
	top: 90.5%;
	transform: rotate(-45.5deg);
	left: 2.5%;
	transform-origin: left bottom;
	width: 139%;
}

.start-game-component {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	width: 100%;
	z-index: 1000;
	color: white;
	bottom: 4rem;
	font-size: 2rem;
}

.buttons {
	display: flex;
	justify-content: center;
	margin-bottom: 3rem;
	width: 100%;
}

.start-blur {
	height: 100vh;
	width: 100vw;
	position: absolute;
	top: 0;
	z-index: 900;
	background-color: black;
	opacity: 90%;
	backdrop-filter: blur(6px);
}
